<!-- style -->
<style lang="less">
@import (less) '../../common/css/common.less';
@module: m-comment;

.@{module} {
	padding-top: 20px;
	padding-bottom: 80px;
    .default_backgroud_5;
    & &-wrap {
        width: 100%;
        height: 100%;
        .default_width_960;
        .default_mar_auto; 
    }
}
.@{module}-content {
	position: relative;
	min-height: 400px;
	overflow: hidden;
	& .comment-left {}
	& .comment-right {
		width: 720px;
		margin-left: 240px;
		& .comment-right-title {
            width: 100%;
            height: 60px;
            line-height: 60px;
            padding-left: 40px;
            .default_font_size_3;
